/*!
 * SCSS Lay out for the pages
 */
 @font-face {
     font-family: 'manga_templebold';
     src: url('mangatb-webfont.woff2') format('woff2'),
          url('mangatb-webfont.woff') format('woff');
     font-weight: normal;
     font-style: normal;
 }

html,
body {
    width: 100%;
    height: 100%;
}

body {
    font-family: 'manga_templebold';
}

hr {
    max-width: 50px;
    border-color: #f05f40;
    border-width: 3px;
}

hr.light {
    border-color: #fff;
}

a {
    color: #f05f40;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

a:hover,
a:focus {
    color: #eb3812;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'manga_templebold';
    -webkit-text-stroke: 2px black;
    color: white;
}

p {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.5;
    color: black;
}

ul {
  list-style-type:: none;
}

.frontpage {
  background-image: url("https://pm1.narvii.com/6097/282e2e0a27d3c7810c49568413feae7ba887c5f4_hq.jpg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  padding-left: 10%;

  ul {
    padding: 30px;
  }

  li {
    font-size: 20px;
    padding: 10px;
  }
}

.blogpage {
  ul {
    padding: 30px;
  }

  li {
    font-size: 20px;
    padding: 10px;
  }
}

.bg-primary {
  background-image: url("https://i.pinimg.com/originals/cc/91/6d/cc916d6a473fa29ec751d1d8a2d769f9.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 100px;
}

.bg-dark {
    color: #fff;
    background-color: #222;
}

.bg-dbz {
  background-image: url("https://i.pinimg.com/originals/cc/91/6d/cc916d6a473fa29ec751d1d8a2d769f9.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.bg-dbz-2 {
  background-image: url("https://images7.alphacoders.com/677/thumb-1920-677266.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

section {
    padding: 50px 0;
}

aside {
    padding: 50px 0;
}

.no-padding {
    padding: 0;
}

.padding {
  padding-left: 25%;
  padding-right: 25%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.navbar-default {
    border-color: rgba(34,34,34,.05);
    font-family: 'manga_templebold';
    background-color: #fff;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.navbar-default .navbar-header .navbar-brand {
    text-transform: uppercase;
    font-family: 'manga_templebold';
    font-weight: 700;
    color: #f05f40;
}

.navbar-default .navbar-header .navbar-brand:hover,
.navbar-default .navbar-header .navbar-brand:focus {
    color: #eb3812;
}

.navbar-default .nav > li>a,
.navbar-default .nav>li>a:focus {
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    color: #222;
}

.navbar-default .nav > li>a:hover,
.navbar-default .nav>li>a:focus:hover {
    color: #f05f40;
}

.navbar-default .nav > li.active>a,
.navbar-default .nav>li.active>a:focus {
    color: #f05f40!important;
    background-color: transparent;
}

.navbar-default .nav > li.active>a:hover,
.navbar-default .nav>li.active>a:focus:hover {
    background-color: transparent;
}

@media(min-width:768px) {
    .navbar-default {
        border-color: rgba(255,255,255,.3);
        background-color: black;
    }

    .navbar-default .navbar-header .navbar-brand {
        color: rgba(255,255,255,.7);
    }

    .navbar-default .navbar-header .navbar-brand:hover,
    .navbar-default .navbar-header .navbar-brand:focus {
        color: #fff;
    }

    .navbar-default .nav > li>a,
    .navbar-default .nav>li>a:focus {
        color: rgba(255,255,255,.7);
    }

    .navbar-default .nav > li>a:hover,
    .navbar-default .nav>li>a:focus:hover {
        color: #fff;
    }

    .navbar-default.affix {
        border-color: rgba(34,34,34,.05);
        background-color: #fff;
    }

    .navbar-default.affix .navbar-header .navbar-brand {
        font-size: 14px;
        color: #f05f40;
    }

    .navbar-default.affix .navbar-header .navbar-brand:hover,
    .navbar-default.affix .navbar-header .navbar-brand:focus {
        color: #eb3812;
    }

    .navbar-default.affix .nav > li>a,
    .navbar-default.affix .nav>li>a:focus {
        color: #222;
    }

    .navbar-default.affix .nav > li>a:hover,
    .navbar-default.affix .nav>li>a:focus:hover {
        color: #f05f40;
    }
}

header {
    position: relative;
    width: 100%;
    min-height: auto;
    text-align: center;
    color: #fff;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
    background-image: url("https://i.pinimg.com/originals/cc/91/6d/cc916d6a473fa29ec751d1d8a2d769f9.png");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
}

header .header-content {
    position: relative;
    width: 100%;
    padding: 100px 15px;
    text-align: center;
}

header .header-content .header-content-inner h1 {
    margin-top: 600px;
    margin-bottom: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 50px;
}

header .header-content .header-content-inner hr {
    margin: 30px auto;
}

header .header-content .header-content-inner p {
    margin-bottom: 50px;
    font-size: 16px;
    font-weight: 300;
    color: black;
    font-weight: bold
}

header .img {
  margin-top: 100px;
  margin-bottom: 100px
}

@media(min-width:768px) {
    header {
        min-height: 100%;
    }

    header .header-content {
        position: absolute;
        top: 50%;
        padding: 0 50px;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
    }

    header .header-content .header-content-inner {
        margin-right: auto;
        margin-left: auto;
        max-width: 1000px;
    }

    header .header-content .header-content-inner p {
        margin-right: auto;
        margin-left: auto;
        max-width: 80%;
        font-size: 18px;
    }
}

.section-heading {
    margin-top: 0;
}

.service-box {
    margin: 50px auto 0;
    max-width: 400px;
}

@media(min-width:992px) {
    .service-box {
        margin: 20px auto 0;
    }
}

@media(max-width:992px) {
    header {
        background-image: url("https://i.pinimg.com/originals/00/3a/aa/003aaabaeb79a4c413605a2e6fc1f56f.jpg");
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
      }
}

.btn-default {
    border-color: #fff;
    color: #222;
    background-color: #fff;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.btn-default:hover,
.btn-default:focus,
.btn-default.focus,
.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    border-color: #ededed;
    color: #222;
    background-color: #f2f2f2;
}

.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    background-image: none;
}

.btn-default.disabled,
.btn-default[disabled],
fieldset[disabled] .btn-default,
.btn-default.disabled:hover,
.btn-default[disabled]:hover,
fieldset[disabled] .btn-default:hover,
.btn-default.disabled:focus,
.btn-default[disabled]:focus,
fieldset[disabled] .btn-default:focus,
.btn-default.disabled.focus,
.btn-default[disabled].focus,
fieldset[disabled] .btn-default.focus,
.btn-default.disabled:active,
.btn-default[disabled]:active,
fieldset[disabled] .btn-default:active,
.btn-default.disabled.active,
.btn-default[disabled].active,
fieldset[disabled] .btn-default.active {
    border-color: #fff;
    background-color: #fff;
}

.btn-default .badge {
    color: #fff;
    background-color: grey;
}

.btn-primary {
    border-color: #f05f40;
    color: #fff;
    background-color: grey;
    -webkit-transition: all .35s;
    -moz-transition: all .35s;
    transition: all .35s;
}

.btn-primary:hover,
.btn-primary:focus,
.btn-primary.focus,
.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
    border-color: #ed431f;
    color: #fff;
    background-color: #ee4b28;
}

.btn-primary:active,
.btn-primary.active,
.open > .dropdown-toggle.btn-primary {
    background-image: none;
}

.btn-primary.disabled,
.btn-primary[disabled],
fieldset[disabled] .btn-primary,
.btn-primary.disabled:hover,
.btn-primary[disabled]:hover,
fieldset[disabled] .btn-primary:hover,
.btn-primary.disabled:focus,
.btn-primary[disabled]:focus,
fieldset[disabled] .btn-primary:focus,
.btn-primary.disabled.focus,
.btn-primary[disabled].focus,
fieldset[disabled] .btn-primary.focus,
.btn-primary.disabled:active,
.btn-primary[disabled]:active,
fieldset[disabled] .btn-primary:active,
.btn-primary.disabled.active,
.btn-primary[disabled].active,
fieldset[disabled] .btn-primary.active {
    border-color: #f05f40;
    background-color: #f05f40;
}

.btn-primary .badge {
    color: #f05f40;
    background-color: grey;
}

.btn {
    border: 0;
    border-radius: 300px;
    text-transform: uppercase;
    font-weight: 700;
}

.btn-xl {
    padding: 15px 30px;
}

::-moz-selection {
    text-shadow: none;
    color: #fff;
    background: #222;
}

::selection {
    text-shadow: none;
    color: #fff;
    background: #222;
}

body {
    webkit-tap-highlight-color: #222;
}

.center {
	text-align: center;
}

.saiyan_portrait {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 100px
}

.saiyan_portrait > div {
    font-size: 20px;
    margin: 50px;
    padding: 20px;
    width: 400px;
    height: 300px;
}

img {
    border:5px solid black;
    margin-bottom: 100px;
}

img:hover {
    webkit-filter: blur(4px);
    filter: blur(4px);
}

.headerimage {
  img {
      border:0px;
      margin-bottom: 50px;
      margin-top: 100px
  }

  img:hover {
      webkit-filter: blur(0px);
      filter: blur(0px);
  }
}


.blogimage {
    img {
        border:0px;
        margin-bottom: 50px;
        margin-top: 30px
  }
}
